import { MOONPAY_WIDGET_CONTAINER_ID } from 'src/constants';
import type {
  MoonPayBuyConfig,
  MoonPayClaimWidgetConfig,
  MoonPayConsumerKycConfig,
  MoonPayDebug,
  MoonPayEnvironment,
  MoonPayLegacyConfig,
  MoonPayNftWidgetConfig,
  MoonPaySellConfig,
  MoonPaySwapConfig,
  MoonPaySwapsCustomerSetupConfig,
  MoonPayTopUpBalanceLedgerWidgetConfig,
  MoonPayTransactionTrackerConfig,
  MoonPayWidgetProps,
  MoonPayWithdrawLedgerWidgetConfig,
  TFlow,
} from './types';

type CommonProps<T extends TFlow> = {
  debug: MoonPayDebug;
  environment: MoonPayEnvironment;
  props: MoonPayWidgetProps<T>;
};

type ConfigConversionParams<T extends TFlow> = T extends 'legacy'
  ? Omit<CommonProps<T>, 'apiKey' | 'signature'>
  : CommonProps<T> & {
    apiKey: string;
    signature: string;
  };

export const getBuyConfigFromProps = ({
  props,
  apiKey,
  debug,
  environment,
  signature,
}: ConfigConversionParams<'buy'>): MoonPayBuyConfig => {
  const {
    // params
    currencyCode,
    defaultCurrencyCode,
    walletAddress,
    walletAddressTag,
    walletAddresses,
    walletAddressTags,
    colorCode,
    theme,
    themeId,
    language,
    baseCurrencyCode,
    baseCurrencyAmount,
    quoteCurrencyAmount,
    lockAmount,
    email,
    externalTransactionId,
    externalCustomerId,
    paymentMethod,
    redirectURL,
    showAllCurrencies,
    showOnlyCurrencies,
    showWalletAddressForm,
    unsupportedRegionRedirectUrl,
    skipUnsupportedRegionScreen,
    baseOrigin,
    // handlers
    onAuthToken,
    onInitiateDeposit,
    onKmsWalletsCreated,
    onLogin,
    onTransactionCompleted,
    onUnsupportedRegion,
    onSwapsCustomerSetupComplete,
    onCloseOverlay,
    onTransactionCreated,
    // auth
    auth,
    useWarnBeforeRefresh,
    // other
    overlayNode,
  } = props;

  const params: MoonPayBuyConfig['params'] = {
    apiKey,
    signature,
    currencyCode,
    defaultCurrencyCode,
    walletAddress,
    walletAddressTag,
    walletAddresses,
    walletAddressTags,
    colorCode,
    theme,
    themeId,
    language,
    baseCurrencyCode,
    baseCurrencyAmount,
    quoteCurrencyAmount,
    lockAmount,
    email,
    externalTransactionId,
    externalCustomerId,
    paymentMethod,
    redirectURL,
    showAllCurrencies,
    showOnlyCurrencies,
    showWalletAddressForm,
    unsupportedRegionRedirectUrl,
    skipUnsupportedRegionScreen,
    baseOrigin,
  };

  const handlers: MoonPayBuyConfig['handlers'] = {
    onAuthToken,
    onInitiateDeposit,
    onKmsWalletsCreated,
    onLogin,
    onTransactionCompleted,
    onUnsupportedRegion,
    onSwapsCustomerSetupComplete,
    onCloseOverlay,
    onTransactionCreated,
  };

  return {
    flow: 'buy',
    variant: props.variant,
    environment,
    params,
    debug,
    handlers,
    auth,
    useWarnBeforeRefresh,
    containerNodeSelector: `#${MOONPAY_WIDGET_CONTAINER_ID}`,
    overlayNode,
  };
};

